import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'

export const Abrigos = ({ user }) => {
    return (
        <div className="Main">
             <p style={{height: '10px', margin: '-10px', marginBottom: '5px',backgroundColor: '#6ccdb3'}}></p>
            <h1 style={{color: 'silver'}}>Abrigos temporários</h1>
            <p style={{height: '2px', backgroundColor: 'silver', marginTop: '-5px', marginBottom: '50px'}}></p>
            <TableContainer component={Paper} style={{ marginTop: '0px' }}>
                <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            <TableCell> <strong>Abrigo (Nome)</strong></TableCell>
                            <TableCell> <strong>Localização</strong></TableCell>
                            <TableCell><strong>Capacidade alocação</strong></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow
                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                        >
                            <TableCell>Escola Industrial de Matundo  </TableCell>
                            <TableCell>Tete Cidade, B. Matundo</TableCell>
                            <TableCell>600</TableCell>
                        </TableRow>

                        <TableRow>
                            <TableCell>Escola Secundária  de Chingodzi</TableCell>
                            <TableCell>Tete Cidade, B. Matundo</TableCell>
                            <TableCell>350</TableCell>
                        </TableRow>

                        <TableRow>
                            <TableCell>Pavilhão polivalente do conselho municipal </TableCell>
                            <TableCell>Tete Cidade, Av. Julios Nherere</TableCell>
                            <TableCell>22</TableCell>
                        </TableRow>
                        
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    )
}