import { Button } from "@material-ui/core"
import axios from "axios"
import { Alert, InputAdornment } from '@mui/material'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import TextField from '@mui/material/TextField'
import { Link } from "react-router-dom"
import { useForm, useStep } from "react-hooks-helper"

/*
                value={email}
                    onChange={setForm}*/
const Login = () => {
    const [formData, setForm] = useForm({
        email: "",
        password: ""
    })

    const { email, password } = formData
    return (
        <div>
            <h3 style={{ borderTop: 'solid 5px #6ccdb3', textAlign: 'center' }}>LOGIN</h3>
            <div style={{ boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px', textAlign: 'justify', padding: '25px', borderRadius: '5px' }}>
                <div style={{ marginLeft: 'auto', marginRight: 'auto', width: '300px' }}>
                    <TextField
                        id="outlined"
                        name="email"
                        value={email}
                        onChange={setForm}
                        label='Usuario'
                        size="small"
                        style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                        InputProps={{
                            startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><AccountCircleIcon /></InputAdornment>
                        }}
                    /> <br></br>
                    <TextField
                        id="outlined"
                        name="password"
                        value={password}
                        type="password"
                        onChange={setForm}
                        label='Senha'
                        size="small"
                        style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                        InputProps={{
                            startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}>*</InputAdornment>
                        }}
                    /> <br></br>


                    <Button color="secondary" variant="contained" style={{ color: 'white', width: '300px', marginTop: '0px', borderRadius: '20px' }}
                        onClick={() => {
                            console.log(formData);
                            const result = Object.keys(formData).filter(el => !formData[el])
                            if (result.length == 0) {
                                axios.post('/api/user/login', { email: formData.email, password: formData.password }).then(res => {
                                    
                                    if (res.data.status === "success") {
                                        window.location.href = '/dashboard'
                                    } else {
                                        alert('Erro do password/email')
                                    }
                                }).catch(e => {
                                    const err = { e }
                                    alert('Erro do password/email')
                                    console.log(err)
                                })
                            } else {
                                alert('Senha &/ou usuario errado(s).')
                            }

                        }}
                    >Entrar</Button>

                    <br></br>
                    <Button fullWidth style={{ marginTop: '5px', color: 'dodgerblue' }}
                        onClick={() => {
                            window.location.href = '/registar'
                        }}
                    >CRIAR CONTA</Button>
                </div>
            </div>
        </div>
    )
}

export default Login 