import { Button } from "@material-ui/core"
import axios from "axios"
import { useEffect } from "react"
import { Link } from "react-router-dom"
import { GrActions } from "react-icons/gr";


const Home = ({ user, isLoggedIn }) => {
    useEffect(() => {
        console.log(user)
        console.log('Is it logged: ' + isLoggedIn)
    }, [])

    return (
        <div>
            <GrActions />
            <h1>Principal</h1>
            <hr></hr>
            {isLoggedIn ? <p>
                <strong>User: </strong> <span>{user.username}</span> <br></br>
                <strong>Tel.: </strong> <span>{user.telefone}</span> <br></br>
            </p> : <></>}
            {isLoggedIn ? <nav>
                <Button
                    color="primary"
                    variant="contained"
                    style={{ textTransform: 'none', borderRadius: '2px', marginTop: '16px', marginRight: '5px', boxShadow: 'none' }}
                    onClick={() => {
                        axios.get('/api/user/logout').then(res => {
                            window.location.href = '/'
                        })
                    }}
                >Sair</Button>
                <Link to='/dashboard'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ borderRadius: '2px', marginTop: '16px', boxShadow: 'none' }}
                    >Dashboard</Button>
                </Link>
            </nav> :
                <Link to='/login'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '2px', marginTop: '16px', boxShadow: 'none' }}
                    >Login</Button>
                </Link>
            }
        </div>
    )
}
export default Home