import { Button, InputAdornment, TextField } from '@material-ui/core'
import axios from 'axios'
import { useForm, useStep } from "react-hooks-helper"

import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { useEffect, useState } from 'react'

export const Register = () => {
  const [formData, setForm] = useForm({
    nome: "",
    telefone: "",
    email: ""
  })

  const [rows, setRows] = useState([])
  const { nome, email, telefone } = formData

  useEffect(() => {
    axios.get('/api/getEntidade').then(res => {
      console.log(res)
      setRows(res.data.data.data)
    })
  }, [])
  
  /*/api/createEntidade*/
  return (<div>
    <div className="Main">
      <TextField
        id="outlined"
        name="nome"
        value={nome}
        onChange={setForm}
        label='Nome da Instituição/Org.'
        size="small"
        style={{ marginBottom: '0px', width: '300px', marginRight: '20px' }}
      />

      <TextField
        id="outlined"
        name="email"
        value={email}
        onChange={setForm}
        label='Correio electrónico'
        size="small"
        style={{ marginBottom: '0px', width: '300px', marginRight: '0px' }}
        InputProps={{
          startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><strong style={{ color: 'green' }}>(email)</strong></InputAdornment>
        }}
      />

      <TextField
        id="outlined"
        name="telefone"
        value={telefone}
        onChange={setForm}
        label='Telefone'
        size="small"
        style={{ marginBottom: '20px', width: '220px', marginRight: '0px' }}
        InputProps={{
          startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><strong style={{ color: 'green' }}>(+258)</strong></InputAdornment>,
          maxLength: 9
        }}
      />
      <br></br>
      <Button color="primary" variant="contained" style={{ color: 'white', marginTop: '0px' }}
        onClick={() => {
          if (Object.keys(formData).filter(el => !formData[el]) == 0) {
            axios.post('/api/createEntidade', formData).then(res => {
              alert('Entidade registada com sucesso.')
            }).catch(e => alert('Error operation'))
          } else {
            alert('Por favor, preencher campos vazios.')
          }
        }}
      >Registar entidade</Button>
    </div>


    <div style={{ border: '1px solid', marginTop: '-80px' }}>
      <TableContainer component={Paper} style={{ marginTop: '0px' }}>
        <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell> <strong>Organização</strong></TableCell>
              <TableCell> <strong>Email</strong></TableCell>
              <TableCell><strong>Telefone</strong></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(el => (<TableRow
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell>{el.nome}</TableCell>
              <TableCell>{el.email}</TableCell>
              <TableCell>{el.telefone}</TableCell>
            </TableRow>))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  </div>)
}