import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Checkbox from '@mui/material/Checkbox';
import IconButton from '@mui/material/IconButton';
import CommentIcon from '@mui/icons-material/Comment';
import { Box, Button, ButtonBase, Divider, InputAdornment, TextField, Typography } from '@material-ui/core';
import LocationCityIcon from '@mui/icons-material/LocationCity';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';

import Collapse from '@mui/material/Collapse';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import axios from 'axios';
import { useForm, useStep } from "react-hooks-helper"

export const Emitir = ({user}) => {
    const [checked, setChecked] = React.useState([0]);
    const [row, setRow] = React.useState([])
    const [checkedB, setCheckedB] = React.useState([0]);
    const [formData, setForm] = useForm({
        nome: "",
        telefone: "",
        email: ""
    })

    const {nome, email, telefone } = formData

    const handleToggle = (value) => () => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
    }




    const handleToggleB = (value) => () => {
        const currentIndex = checkedB.indexOf(value);
        const newChecked = [...checkedB];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setCheckedB(newChecked);
    };

    const [openA, setOpenA] = React.useState(false);
    const handleClickA = () => {
        setOpenA(!openA);
    };

    React.useEffect(() => {
      axios.get('/api/getEntidade').then(res => {
        if(res.data.status === "success"){
            setRow(res.data.data.data)
            console.log(row);
        }else{
            setRow([])
        }
      })
    }, [])
    


    return (
        <div className="Main">
            <p style={{ backgroundColor: 'red', margin: '-10px', color: 'white', padding: '5px', textAlign: 'center', fontSize: '17px', textTransform: 'uppercase' }}><strong>Autoridades</strong></p>
            <List sx={{ width: '100%', marginTop: '10px', bgcolor: 'background.paper' }}>
                {row.length == 0 ? '': row.map((value) => (
                        <ListItem
                            key={value._id}
                            secondaryAction={
                                <IconButton edge="end" aria-label="comments">
                                    <LocationCityIcon />
                                </IconButton>
                            }
                            disablePadding
                        >
                            <ListItemButton role={undefined} onClick={handleToggle(value.nome)} dense>
                                <ListItemIcon>
                                    <Checkbox
                                        edge="start"
                                        checked={checked.indexOf(value.nome) !== -1}
                                        tabIndex={-1}
                                        disableRipple
                                        inputProps={{ 'aria-labelledby': value._id }}
                                    />
                                </ListItemIcon>
                                <ListItemText id={value._id} primary={`${value.nome}`} />
                            </ListItemButton>
                        </ListItem>))}
            </List>


            <List>
                <ListItemButton onClick={handleClickA}>
                    <ListItemIcon style={{ color: 'dodgerblue' }}>
                        <LocationCityIcon />
                    </ListItemIcon>
                    <ListItemText style={{ marginLeft: '-20px' }} primary="Outros" secondary="Instituição/Organização" />
                    {openA ? <ExpandLess /> : <ExpandMore />}
                </ListItemButton>

                <Collapse in={openA} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                        <ListItem>
                            <TextField
                                id="outlined"
                                name="nome"
                                value={nome}
                                onChange={setForm}
                                label='Nome da Instituição/Org.'
                                size="small"
                                style={{ marginBottom: '0px', width: '300px', marginRight: '20px' }}
                            />
                        </ListItem>

                        <ListItem>
                            <TextField
                                id="outlined"
                                name="email"
                                value={email}
                                onChange={setForm}
                                label='Correio electrónico'
                                size="small"
                                style={{ marginBottom: '0px', width: '270px', marginRight: '0px' }}
                                InputProps={{
                                    startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><strong style={{ color: 'green' }}>(email)</strong></InputAdornment>
                                }}
                            />
                        </ListItem>

                        <ListItem>
                            <TextField
                                id="outlined"
                                name="telefone"
                                value={telefone}
                                onChange={setForm}
                                label='Telefone'
                                size="small"
                                style={{ marginBottom: '20px', width: '220px', marginRight: '0px' }}
                                InputProps={{
                                    startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><strong style={{ color: 'green' }}>(+258)</strong></InputAdornment>
                                }}
                            />
                        </ListItem>

            
                            <Button color="primary" variant="contained" style={{ color: 'white', float: 'right', marginTop: '-20px' }}
                                onClick={() => {
                                    if(Object.keys(formData).filter(el => !formData[el]) == 0){
                                        axios.post('/api/createEntidade', formData).then(res => {
                                            alert('Entidade registada. E alerta emitido com sucesso!')
                                        })
                                    }else{
                                        alert('Por favor, preencher campos vazios.')
                                    }
                                }}
                            >Emitir para esta entidade</Button>
                     
                    </List>
                </Collapse>
            </List>

{/*
            <Divider />
            <p style={{ backgroundColor: 'red', margin: '-10px', marginTop: '20px', color: 'white', padding: '5px', textAlign: 'center', fontSize: '17px', textTransform: 'uppercase' }}><strong>Canal de Notificação</strong></p>
            <List sx={{ width: '100%', marginTop: '10px', bgcolor: 'background.paper' }}>
                {['Correio electrónico', 'Telefone'].map((value) => {
                    const labelId = `checkbox-list-label-${value}`;
                    return (
                        <ListItem
                            key={value}
                            secondaryAction={
                                <IconButton edge="end" aria-label="comments">

                                </IconButton>
                            }
                            disablePadding
                        >
                            <ListItemButton role={undefined} onClick={handleToggleB(value)} dense>
                                <ListItemIcon>
                                    <Checkbox
                                        edge="start"
                                        checked={checkedB.indexOf(value) !== -1}
                                        tabIndex={-1}
                                        disableRipple
                                        inputProps={{ 'aria-labelledby': labelId }}
                                    />
                                </ListItemIcon>
                                <ListItemText id={labelId} primary={`${value}`} />
                            </ListItemButton>
                        </ListItem>
                    );
                })}
            </List>
            */}

            <Divider />
            <Typography align='center'>
                <Button color="secondary" variant="contained" style={{ color: 'white', width: '200px', marginTop: '10px', borderRadius: '20px' }}
                    onClick={() => {
                        var a = []
                        for (let index = 0; index < checked.length; index++) {
                            if(index>0){
                                a.push(checked[index])
                            }
                        }

                        if(a.length == 0){
                            alert('Seleccione a entidade que pretende emitir o alerta.')
                        }else{
                            const dd = {
                                entidades: a,
                                user: user
                            }
                            console.log(dd)
                            axios.post('/api/emit', dd).then(res => {
                                console.log(res);
                                alert('Alerta emitido com sucesso.')
                            })
                        }
 
                    }}
                >Emitir</Button>
            </Typography>
        </div>
    )
}