import React, { useState } from "react";
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import WifiTetheringErrorIcon from '@mui/icons-material/WifiTetheringError';
import HistoryIcon from '@mui/icons-material/History';
import InfoIcon from '@mui/icons-material/Info';
import MedicationIcon from '@mui/icons-material/Medication';
import CrisisAlertIcon from '@mui/icons-material/CrisisAlert';
import SosIcon from '@mui/icons-material/Sos';
import ReactDOM from 'react-dom/client'
import logo from '../assets/image/logo.jpg'
import {
    AppBar,
    Toolbar,
    Avatar,
    Box,
    Divider,
    IconButton,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    makeStyles,
    CssBaseline,
    Drawer,
    Typography,
    Button,
    Switch
} from "@material-ui/core";
import {
    Apps,
    Menu,
    ContactMail,
    AssignmentInd,
    Home
} from "@material-ui/icons";

import ListSubheader from '@mui/material/ListSubheader';
import ListItemButton from '@mui/material/ListItemButton';
import Collapse from '@mui/material/Collapse';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import DraftsIcon from '@mui/icons-material/Drafts';
import SendIcon from '@mui/icons-material/Send';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import StarBorder from '@mui/icons-material/StarBorder';

import CommitIcon from '@mui/icons-material/Commit';
import { Main } from "../components/Main";
import { Sobre } from "../components/Sobre";
import { Emitir } from "../components/Emitir";
import { Historico } from "../components/Historico";
import { Abrigos } from "../components/Abrigos";
import axios from "axios";
import { Register } from "../components/Register";
import { Alert, Users } from "../components/Alert";

const useStyles = makeStyles((theme) => ({
    menuSliderContainer: {
        width: 300,
        background: "#fff",
        height: "100%"
    },
    avatar: {
        margin: "0.5rem auto",
        padding: "1rem",
        width: theme.spacing(13),
        height: theme.spacing(13)
    },
    listItem: {
        color: ""
    }
}));


export default function Admin({ user }) {
    const classes = useStyles();
    const [open, setOpen] = useState(false);
    const [checked, setChecked] = React.useState(false);
    const [checkedB, setCheckedB] = React.useState(false);
    const [checkedC, setCheckedC] = React.useState(false);

    const toggleSlider = () => {
        setOpen(!open);
    };

    const [openA, setOpenA] = React.useState(false);
    const handleClickA = () => {
        setOpenA(!openA);
    };

    const [openB, setOpenB] = React.useState(false);
    const handleClickB = () => {
        setOpenB(!openB);
    };
    //#6ccdb3


    const handleChange = (event) => {
        setChecked(event.target.checked);
    };

    const handleChangeB = (event) => {
        setCheckedB(event.target.checked);
    };

    const handleChangeC = (event) => {
        setCheckedC(event.target.checked);
    };

    const sideList = () => (
        <Box className={classes.menuSliderContainer} component="div">
            <div style={{ backgroundColor: '#6ccdb3', height: '200px' }}>
                <p style={{ height: '5px', backgroundColor: 'red', marginBottom: '2px', width: '200px', marginLeft: 'auto', marginRight: 'auto' }}></p>
                <img src={logo} alt="logo-app" style={{ width: '200px', height: '95px', display: 'block', margin: '0 auto' }} />
            </div>
            <Divider />
            <ListItemButton style={{ marginTop: '0px' }}
                onClick={() => {
                    ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Main user={user} /></React.StrictMode>)
                    setOpen(!open)
                }}
            >
                <ListItemIcon>
                    <AccountCircleIcon />
                </ListItemIcon>
                <ListItemText style={{ marginLeft: '-20px' }}><strong>Meu perfil</strong></ListItemText>
            </ListItemButton>

            <Divider />
            <List>


                <Collapse in={openA} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                        <ListItem>
                            <ListItemIcon>
                                <CommitIcon />
                            </ListItemIcon>
                            <ListItemText primary="Familiar desaparecido" />
                            <Switch
                                color="secondary"
                                checked={checkedC}
                                onChange={handleChangeC}
                                inputProps={{ 'aria-label': 'controlled' }}
                            />
                        </ListItem>
                    </List>
                </Collapse>

                <ListItemButton
                    onClick={() => {
                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Register user={user} /></React.StrictMode>)
                        setOpen(!open)
                    }}
                >
                    <ListItemText style={{ marginLeft: '0px' }} primary="Registar entidades" />
                </ListItemButton>

                <ListItemButton
                    onClick={() => {
                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Alert user={user} /></React.StrictMode>)
                        setOpen(!open)
                    }}
                >
                    <ListItemText style={{ marginLeft: '0px' }} primary="Alertas" />
                </ListItemButton>


            </List>
        </Box>
    )

    return (
        <div>
            <CssBaseline />
            <Box component="nav">
                <AppBar style={{ background: 'red' }}>
                    <Toolbar>
                        <IconButton onClick={toggleSlider} style={{ color: '#fff' }}>
                            <Menu />
                        </IconButton>
                        <Typography><strong>DASHBOARD |</strong> <span style={{ fontSize: '13px' }}>{user.email}</span></Typography>
                        <Drawer open={open} anchor="left" onClose={toggleSlider}>
                            {sideList()}
                        </Drawer>
                        <Button color="primary" variant="contained" style={{ float: 'right', marginLeft: '20px' }}
                            onClick={() => {
                                axios.get('/api/user/logout').then(res => {
                                    window.location.href = '/'
                                })
                            }}
                        >SAIR</Button>
                    </Toolbar>

                </AppBar>

            </Box>

            <div id="desktop" style={{ marginTop: '100px' }}>
                <Main user={user} />
            </div>
        </div>
    )
}
