import { Button } from "@material-ui/core"
import axios from "axios"
import { Alert, InputAdornment } from '@mui/material'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import TextField from '@mui/material/TextField'

/*
                value={email}
                    onChange={setForm}*/
const Sign = () => {
    return (
        <div>
            <h3 style={{borderTop: 'solid 5px #6ccdb3', textAlign: 'center'}}>CRIAR CONTA</h3>
            <div style={{ boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px', textAlign: 'justify', padding: '25px', borderRadius: '5px' }}>
                <TextField
                    id="outlined"
                    name="nome"
                    label='Nome completo'
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><AccountCircleIcon /></InputAdornment>
                    }}
                /> <br></br>
                <TextField
                    id="outlined"
                    name="usuario"
                    label='Usuário'
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><AccountCircleIcon /></InputAdornment>
                    }}
                /> <br></br>
                 <TextField
                    id="outlined"
                    name="usuario"
                    label='Usuário'
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}>Email</InputAdornment>
                    }}
                /> <br></br>
                 <TextField
                    id="outlined"
                    name="telefone"
                    label='Telefone'
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}>(+258)</InputAdornment>
                    }}
                /> <br></br>
                <TextField
                    id="outlined"
                    name="password"
                    label='Senha'
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}>*</InputAdornment>
                    }}
                /> <br></br>

                 <TextField
                    id="outlined"
                    name="password"
                    label='Confirmar Senha'
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}>*</InputAdornment>
                    }}
                /> <br></br>


                <Button color="secondary" variant="contained" style={{ color: 'white', width: '200px', marginTop: '0px', borderRadius: '20px' }}
                      onClick={() => {
                        axios.post('/api/user/login', { email: 'dark@io.com', password: 'vicenty5@' }).then(res => {
                            window.location.href = '/'
                        }).catch(e => {
                            const err = { e }
                            console.log(err)
                        })
                    }}
                >Criar</Button>
                <br></br>
                 <Button style={{marginTop: '5px'}}
                    onClick={() => {
                        window.location.href = '/login'
                    }}
                    >JÁ ESTOU INSCRITO! <span style={{color: 'dodgerblue'}}> ENTRAR</span></Button>
            </div>
        </div>
    )
}

export default Sign