
import '../assets/css/comps.css'
import img from '../assets/image/user.jpg'

export const Main = ({user}) => {
    return (
            <div className="Main">
                <p style={{height: '10px', backgroundColor: '#6ccdb3', margin: '-10px'}}></p>
                <div className="row">
                    <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6 col-xs-offset-0 col-sm-offset-0 col-md-offset-3 col-lg-offset-3 toppad">
                        <div className="panel panel-primary">
                            <div className="panel-heading"> <span style={{float: 'right', marginTop: '10px', color: 'silver'}}><strong>Meu perfil</strong></span>
                            <img alt="" src={img} className="img"/> 
                                <h3 className="panel-title">{user.username}</h3>
                            </div>
                            <div className="panel-body">
                                <div className="row">
                                    <div className="col-md-3 col-lg-3 " align="center"></div>
                                    <div className=" col-md-9 col-lg-9 ">
                                        <table className="table table-user-information">
                                            <tbody>
                                                <tr>
                                                    <td>Email:</td>
                                                    <td>{user.email}</td>
                                                </tr>
                                                <tr>
                                                    <td>Telefone (+258):</td>
                                                    <td>{user.telefone}</td>
                                                </tr>
                                                <tr>
                                                    <td>ID</td>
                                                    <td style={{color: 'green'}}>{user._id}</td>
                                                </tr>
                                             
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    )
}