import { Button } from "@material-ui/core"
import axios from "axios"
import { InputAdornment } from '@mui/material'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import TextField from '@mui/material/TextField'
import { useEffect, useState } from "react"

import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'

export const Alert = () => {
  const [rows, setRows] = useState([])
  useEffect(() => {
    axios.get('/api/getEmit').then(res => {
      console.log(res);
      setRows(res.data.data.data)
    })
  }, [])

  return (
    <div className="Main">
      <TableContainer component={Paper} style={{ marginTop: '0px' }}>
        <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell> <strong>Vitima</strong></TableCell>
              <TableCell> <strong>Email</strong></TableCell>
              <TableCell><strong>Telefone</strong></TableCell>
              <TableCell><strong>Estado</strong></TableCell>
              <TableCell><strong>Mensagem de alerta</strong></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(el => (<TableRow
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell>{el.user.username}</TableCell>
              <TableCell>{el.user.email}</TableCell>
              <TableCell>{el.user.telefone}</TableCell>
              <TableCell>[Em perigo, Seguro/Em seguranca, Familiar desaparecido]</TableCell>
              <TableCell><p style={{textAlign: 'justfy', backgroundColor: 'goldenrod', padding: '5px', borderRadius: '5px' ,color: '#fff'}}>
              Ocorrência da subida <br></br>do caudal do rio Rovubué – Tete.<br></br> <br></br>
              <span><strong>Salve-o</strong></span>
                </p></TableCell>
            </TableRow>))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}