import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'

export const Historico = ({ user }) => {
    return (
        <div className="Main">
             <p style={{height: '10px', margin: '-10px', marginBottom: '5px',backgroundColor: '#6ccdb3'}}></p>
            <h1 style={{color: 'silver'}}>Histórico de inundações</h1>
            <p style={{height: '2px', backgroundColor: 'silver', marginTop: '-5px', marginBottom: '50px'}}></p>
            <TableContainer component={Paper} style={{ marginTop: '0px' }}>
                <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            <TableCell> <strong>Ano</strong></TableCell>
                            <TableCell> <strong>Nº de familias afectadas</strong></TableCell>
                            <TableCell><strong>Nº de mortes registadas</strong></TableCell>
                            <TableCell><strong>Fonte</strong></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow
                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                        >
                            <TableCell>2019</TableCell>
                            <TableCell>997</TableCell>
                            <TableCell>6</TableCell>
                            <TableCell>INGD (2023)</TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>2022</TableCell>
                            <TableCell>952</TableCell>
                            <TableCell>3</TableCell>
                            <TableCell>INGD (2023)</TableCell>
                        </TableRow>

                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    )
}